import React, { Component } from 'react'
import PricesP from './PricesP'
import { CoinApi } from '../../api'

export default class PricesC extends Component {
    state = {
        loading: true,
        error: null,
        data: null
    }
    async componentDidMount() {
        try {
            const { data } = await CoinApi.homePage()
            const sortedData = data.sort((a, b) => { return a.rank - b.rank })
            this.setState({ data: sortedData })
        } catch (error) {
            this.setState({ error: 'Api Server is Not Working!' })
        }
        finally {
            this.setState({ loading: false })
        }
    }
    render() {
        const { loading, error, data } = this.state
        return (
            <PricesP data={data} loading={loading} error={error} />
        )
    }
}
