import React, { Component } from 'react'
import ExchangesP from './ExchangesP'
import { CoinApi } from '../../api'

export default class ExchangesC extends Component {
    state = {
        loading: true,
        error: null,
        data: null
    }
    async componentDidMount() {
        try {
            const { data } = await CoinApi.exchanges()
            this.setState({ data })
        } catch (error) {
            this.setState({ error: 'Api Server is Not Working!' })
        }
        finally {
            this.setState({ loading: false })
        }
    }
    render() {
        const { loading, error, data } = this.state
        return (
            <ExchangesP data={data} loading={loading} error={error} />
        )
    }
}
