import React from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from 'prop-types'
import styled from 'styled-components'

const Header = styled.div`
    width:100%;
    margin:auto;
    padding:30px 0 0;
    background:black;
    h1{
      margin:auto;
      font-size:55px;
      font-weight:600;
      padding:10px 0 0 0;
      text-align:center;
      color:white;  
      a{
        text-decoration:none;
        color:inherit
      }
      @media screen and (max-width:768px){
      font-size:40px;
    }}
`
const List = styled.ul`
    width:55%;
    min-width:50%;
    margin: auto;
    padding: 30px 0 0;
    display:flex;
    @media screen and (max-width:768px){
      width:100%;
      justify-content:space-evenly;
    }
`
const ListItem = styled.li`
    margin:auto;
    text-align:center;
    padding: 10px 40px 15px;
    color:${props => props.current ? 'yellow' : 'white'};
    background:${props => props.current ? 'red' : 'black'};
    font-weight:600;
    font-size:25px;
    border-radius:5px 5px 0 0;
    transition:0.3s all;
    @media screen and (max-width:768px){
      color:${props => props.current ? 'red' : 'white'};
      width:100%;
      font-size:20px;
      padding:10px 5px;
      background:black;
    }
`
const HLink = styled(Link)`
    color:inherit;
    text-decoration: none;
    font-size: inherit;
`

const headerProps = ({ location: { pathname } }) => {
  return (
    <Header>
      <h1><a href='https://junchi86.github.io/index' rel="noopener noreferrer" target="_blank">Coin Explorer</a></h1>
      <List>
        <ListItem current={pathname === "/"}>
          <HLink to="/">Prices</HLink>
        </ListItem>
        <ListItem current={pathname === "/exchanges"}>
          <HLink to="/exchanges">Exchanges</HLink>
        </ListItem>
        <ListItem current={pathname === "/coins"}>
          <HLink to="/coins">Coins</HLink>
        </ListItem>
      </List>
    </Header >
  )
}

headerProps.propTypes = {
  location: PropTypes.shape({
    pathname: PropTypes.string.isRequired
  })
}

export default withRouter(headerProps)
