import React from "react";
import Loading from "../../Components/Loading";
import styled from 'styled-components'
import PropTypes from 'prop-types'

const Container = styled.ul`
    max-width:50%;
    margin: 0 auto;
    padding-top: 50px;
    @media screen and (max-width:768px){
    padding-top: 10px;
    max-width:100%;
    }
`
const ListItem = styled.li`
    font-size:18px;
    color:white;
    padding:5px 0;
    display:flex;
    justify-content:center;
    width:100%;
    margin:auto;
    @media screen and (max-width:768px){
        width:90%;
        margin:auto
    }
`
const Rank = styled.div`
    font-size:20px;
    font-weight:700;
    flex:1;
    text-align:center;
    border:1px solid white;
    padding:5px;
    @media screen and (max-width:768px){
        font-size:15px;
        font-weight:500;
        padding:0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
`
const Contents = styled.div`
    flex:1;
    border:1px solid white;
    border-left:0px;
    text-align:center;
    padding:5px;
    background:rgba(255,255,255,0.4);
    font-weight:500;
    color:white;
    @media screen and (max-width:768px){
        font-size:15px;
        padding:0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

`
const PricesP = ({ loading, error, data }) => {
    return (
        <>
            {loading ? <Loading /> :
                <Container>{
                    data.map(i =>
                        <ListItem key={i.rank}>
                            <Rank>{i.name}/{i.symbol}</Rank>
                            <Contents>$ {i.quotes.USD.price}</Contents>
                        </ListItem>)
                }</Container>
            }
        </>
    )
}

PricesP.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}

export default PricesP