import React from 'react'
import styled from 'styled-components'

const Loading = styled.div`
    text-align:center;
    color:white;
    margin:auto;
    font-size:30px;
    font-weight:600;
    padding:200px 0 0 0;
    @media screen and (max-width:768px){
        padding: 50px 0 0 0;
        font-size: 25px;
    }
`

export default () => {
    return (
        <Loading>I'm Loading!<br/><br/>Responsive Web Page</Loading>
    )
}