import React, { Component } from 'react'
import CoinsP from './CoinsP'
import { CoinApi } from '../../api'

export default class CoinsC extends Component {
    state = {
        loading: true,
        error: null,
        data: null
    }
    async componentDidMount() {
        try {
            const { data } = await CoinApi.coins()
            const firstSortedData = data.sort((a, b) => { return a.rank - b.rank })
            const sortedData = firstSortedData.filter(i => i.rank !== 0)
            this.setState({ data: sortedData })
        } catch (error) {
            this.setState({ error: 'Api Server is Not Working!' })
        }
        finally {
            this.setState({ loading: false })
        }
    }
    render() {
        const { loading, error, data } = this.state
        return (
            <CoinsP data={data} loading={loading} error={error} />
        )
    }
}
