import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

const ToTop = styled.div`
        display:block;
        opacity:1;
        visibility:visible;
        position:fixed;
        bottom:4vh;
        right:10vh;
        width:70px;
        height:70px;
        z-index:50;
        text-align:center;
        line-height:70px;
        background-color:rgba(255,255,255,0.7);
        border:1px solid pink;
        color:white;
        border-radius:5px;
        font-size:30px;
        transition:0.3s all;
        cursor: pointer;
        @media screen and (max-width:768px) {
            width:45px;
            height:45px;
            line-height:45px;
            bottom:5px;
            right:5px;
            font-size:23px;
    }
`

function Footer() {
    const [state, setState] = useState(true)
    const toggleVisibility = () => {
        if (window.pageYOffset > 300) {
            setState(false);
        } else {
            setState(true);
        }
    }
    useEffect(() => {
        window.addEventListener("scroll", toggleVisibility);
        return () => {
            window.removeEventListener("scroll", toggleVisibility);
        };
    });
    document.addEventListener('scroll', (e) => { })

    const toTheTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }

    return (
        <ToTop style={state ? { visibility: 'hidden', opacity: '0' } : { visibility: 'visible', opacity: '1' }} onClick={toTheTop}><span role='img' aria-label='to the top'>🔺</span></ToTop>
    )
}

export default Footer

